import { getAllNoteService, saveLikeService, saveReplyInforService, delCommentInforService, delNoteDataService, addNoteService, saveReportNoticeService, addCommentInforService, getMineMessageCountService,getNoteDetailService, getOwnerNotesService } from '../../services/findService';
const state = {
    pageNumber: 1,
    scrollPosition: 0,
    newCommitType: false,
    addNotePageParams: '',    //
    hasPopWin: false, //
    closePopWin: false, //
}

//  getters demo
var getters = {
    // refreshPage(state) {
    //     return state.commitType;
    // }
}

const actions = {
    //
    getNotesAction(context, reqData, isShowLoading) {
        return new Promise((resolve, reject) => {
            getAllNoteService(reqData, isShowLoading).then(result => {
                context.commit('pageNumChange', { pageNumber: reqData.page });
                result.resultCode == '0' ? resolve(result) : reject(result.msg);
            }, fail => {
                reject(fail);
            })
        })
    },
    //
    saveLikeAction(context, reqData) {
        return new Promise((resolve, reject) => {
            saveLikeService(reqData).then(result => {
                result.resultCode == '0' ? resolve(result) : reject(result.msg);
            }, fail => {
                reject(fail);
            })
        })
    },

    //
    delNoteDataAction(context, noteCode) {
        return new Promise((resolve, reject) => {
            delNoteDataService(noteCode, context.rootState.user.userInfo.OWNER_CODE).then(result => {
                result.resultCode == '0' ? resolve(result) : reject(result.msg);
            }).catch(error => {
                reject(error);
            })
        })
    },

    //
    delCommentInforAction(context, reqParam) {
        return new Promise((resolve, reject) => {
            delCommentInforService(reqParam.noteCode, reqParam.evalCode).then(result => {
                result.resultCode == '0' ? resolve(result) : reject(result.msg)
            }).catch(error => {
                reject(error);
            })
        })
    },

    //
    addNoteAction(context, reqParam) {
        return new Promise((resolve, reject) => {
            addNoteService(reqParam).then(result => {
                result.resultCode == '0' ? resolve(result) : reject(result.msg)
            }).catch(error => {
                reject(error)
            })
        })
    },

    //
    saveReportNoticeAction(context, reportData) {
        return new Promise((resolve, reject) => {
            saveReportNoticeService(reportData).then(result => {
                result.resultCode == '0' ? resolve(result) : reject(result.msg)
            }).catch(error => {
                reject(error)
            })
        })
    },

    //
    addCommentAction(context, commentData) {
        return new Promise((resolve, reject) => {
            addCommentInforService(commentData).then(result => {
                result.resultCode == '0' ? resolve(result) : reject(result.msg)
            }).catch(error => {
                reject(error)
            })
        })
    },

    //
    saveReplyAction(context, replyData) {
        return new Promise((resolve, reject) => {
            saveReplyInforService(replyData).then(result => {
                result.resultCode == '0' ? resolve(result) : reject(result.msg)
            }).catch(error => {
                reject(error)
            })
        })
    },

    // 
    getMineMessageCountAction(context, replyData, isShowLoading){
        return new Promise((resolve, reject) => {
            getMineMessageCountService(replyData, isShowLoading).then(result => {
                result.resultCode == '0' ? resolve(result) : reject(result.msg)
            }).catch(error => {
                reject(error)
            })
        })
    },

    // 
    getNoteDetailAction(context, replyData){
        return new Promise((resolve, reject) => {
            getNoteDetailService(replyData).then(result => {
                result.resultCode == '0' ? resolve(result) : reject(result.msg)
            }).catch(error => {
                reject(error)
            })
        })
    },

    // 
    getOwnerNotesAction(context, replyData){
        return new Promise((resolve, reject) => {
            getOwnerNotesService(replyData).then(result => {
                result.resultCode == '0' ? resolve(result) : reject(result.msg)
            }).catch(error => {
                reject(error)
            })
        })
    },

    //, true:false
    setFindPopWinFlag(context, flag) {
        console.log("[vuex.find] setFindPopWinFlag: "+flag);
        context.commit('setFindPopWinFlag', flag);
    },

    //true:, false:
    setFindClosePopWinFlag(context, flag) {
        console.log("[vuex.find] setFindClosePopWinFlag: "+flag);
        context.commit('setFindClosePopWinFlag', flag);
    },

}

const mutations = {
    //
    pageNumChange(state, payload) {
        if (payload.custPageNum) {
            state.pageNumber = payload.pageNumber
        } else {
            state.pageNumber += 1;
        }
        console.log(state.pageNumber )
    },
    //findpage
    saveFindScollPosition(state, position) {
        state.scrollPosition = position;
    },

    // 
    setAddNotePageParams(state, param) {
        state.addNotePageParams = param;
    },

    // , true:false
    setFindPopWinFlag(state, flag) {
        state.hasPopWin = flag;
    },

    // true:, false:
    setFindClosePopWinFlag(state, flag) {
        state.closePopWin = flag;
    },
}
// 
export default {
    state,
    getters,
    actions,
    mutations
}
