const state = {
    orderListPopupFlag: false, //
};

//  getters
let getters = {
    getOrderListPopupFlag(state) {
        return state.orderListPopupFlag
    }
};

const actions = {
    //
    setOrderListPopupFlag(context, flag) {
        console.log("[vuex.property] setOrderListPopupFlag: "+flag);
        context.commit('setOrderListPopupFlag', flag);
    }
};

const mutations = {
    // 
    setOrderListPopupFlag(state, flag) {
        console.log("stateset");
        state.orderListPopupFlag = flag;
    }
};

// 
export default {
    state,
    getters,
    actions,
    mutations
}
