routes.push(...RouterThemeL.routerThemeL)

// add route path
routes.forEach(route => {
  route.path = route.path || '/' + (route.name || '');
});

const router = new Router({ routes });

router.beforeEach((to, from, next) => {
  const title = to.meta && to.meta.title;
  if (title) {
    document.title = title;
  }
  //add for 
  //Vue.prototype.$curTheme = localStorage.getItem("curTheme") ? localStorage.getItem("curTheme") : "0";
  console.log("router.beforeEach"+Vue.prototype.$curTheme)
  if (Vue.prototype.$curTheme === '1') {
    //
    let hasPage = JSON.stringify(routes).indexOf(to.path+"ThemeL") != -1;
    if (hasPage) {
      let newPage = to.path+"ThemeL";
      let newName = to.name+"ThemeL";
      //router.replace(newPage);
      //next(newPage);
      let newRoute = {path:newPage,name:newName};
      if (JSON.stringify(to.query) != "{}") {
        newRoute.query = to.query;
      } else if (JSON.stringify(to.params) != "{}") {
        newRoute.params = to.params;
      }
      console.log(""+JSON.stringify(newRoute))
      next(newRoute);
      return;
    }
  }
  //end
  next();
});

