export { getDoorListService,getLinglingDoorInfoService,openLinglingRemoteDoorService,addRecordService,addPointService,getLeeLenDoorInfoService,openLeeLenRemoteDoorService,getOwnerDoorListService,setDftDoorService,cancelDftDoorService}

//
const getDoorListService = (params) => {
    return new Promise((resolve, reject) => {
        http.post('xwj-llkm/phoneDoorMng/getDoorList', params, true, 'baseUrl', false).then(result => {
            resolve(result);
        }).catch(error => {
            reject(error);
        })

    });
}


