<template>
  <div class="phoneDoor">
    <!--  -->
    <img
      width="60"
      class="phoneDoorStyle"
      :src="isOpening?loadingImg:phoneImg"
      @click="doorPress()"
    />
    <!--  -->
    <div
      v-if="isPopShow"
      :class="{themeL:bindThemeL}"
      class="phonedoor-popup-container"
      style="background: rgba(0, 0, 0,0.5);"
    >
      <div class="door-popup-body">
        <img :src="closeImg" class="popcloseBtn" @click="closePopup()" />

        <van-image fit="fill" :src="backImg" />
        <div id="qrArea" v-if="isLLQrcode">
          <div class="failText"></div>
          <div class="QRCode">
            <canvas id="phoneDoorQrcode" class="phoneDoorQrcode"></canvas>
          </div>
          <!-- <div class="QRCodeRefresh hide" style="position: absolute;bottom: 0;width: 100%;">
          <img
            style="position:relative;top: 2px"
            src="img/phonedoor/pic_tips_refresh.png"
            onclick="refreshQRcode()"
          />
          </div>-->

          <div class="lineBoxStyle">
            <div class="lineStyle"></div>
          </div>
        </div>
        <div class="opemDoorBox" v-show="isRemote">
          <div class="failMsg"></div>
          <div class="share" id="shareScrollbar">
            <div
              class="leftImg"
              v-for="(x,index) in doorList"
              :key="index"
              @click="doorRemoteOpen(x)"
            >
              <button class="doorBtn" v-if="x.openType>3">{{x.deviceName}}</button>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import {
  getDoorListService,
  openLinglingRemoteDoorService,
  getLinglingDoorInfoService,
  addRecordService,
  addPointService,
  getLeeLenDoorInfoService,
  openLeeLenRemoteDoorService
} from "../../services/phoneDoorService";
import QRCode from "qrcode"; //
import { store, storage } from "../../utils/storage";
import { mapState } from "vuex";
export default {
  name: "phoneDoor",
  data() {
    return {
      doorList: [], //
      isLLQrcode: false, //
      isLLBluetooth: false, //
      isLeeLenBluetooth: false, //
      isRemote: false, //
      linglingKeys: "", //key
      linglingDoorInfos: [], //
      LLingID: "", //ID
      qrcode: "", //
      qrCanvas: null,
      isPopShow: false, //
      openStart: null, //
      isOpening: false, //
      popupTimer: null, //
      btnTimer: null,
      isCanvas: true,
      closeImg: require("../../assets/images/home/property/icon_phoneDoor_popup_close.png"),
      phoneImg: require("../../assets/images/home/property/icon_phoneDoor_open.png"),
      backImg: require("../../assets/images/home/property/icon_phoneDoor_popup_bg.png"),
      loadingImg: require("../../assets/images/home/property/icon_phoneDoor_loading.gif"),
      userInfo: {},
      /**/
      bindThemeL: this.$curTheme == 0 ? false : true
    };
  },
  computed: {
    //state
    phoneDoorRefresh() {
      return this.$store.state.home.phoneDoorRefresh;
    }
  },
  watch: {
    //state
    phoneDoorRefresh(val) {
      console.log("valvalvalvalvalvalvalvalval" + val);
      if (val) {
        this.$store.dispatch("setPhoneDoorRefreshFlag", false);
        this.doorList = [];
        (this.isLLQrcode = false), //
          (this.isLLBluetooth = false), //
          (this.isLeeLenBluetooth = false), //
          (this.isRemote = false), //
          (this.linglingKeys = ""), //key
          (this.linglingDoorInfos = []), //
          (this.LLingID = ""), //ID
          (this.qrcode = ""), //
          (this.isPopShow = false), //
          (this.openStart = null), //
          (this.isOpening = false), //
          clearTimeout(this.popupTimer);
        clearTimeout(this.btnTimer);
        this.getDoorList();
      }
    }
  },
  mounted() {
    this.getDoorList();
  },
  methods: {
    /* ************************************************************************** */
    /*  */
    getDoorList() {
      console.log("getDoorList");
      let reqData = {
        NEIGH_NO: this.$userInfo.AREA_NO,
        NEIGH_STRUCTURE: this.$userInfo.ROOM_NO,
        AREA_CODE: this.$userInfo.AREA_CODE,
        BUILD_CODE: this.$userInfo.BUILD_CODE,
        UNIT_CODE: this.$userInfo.UNIT_NO,
        ROOM_CODE: this.$userInfo.ROOM_CODE
      };
      getDoorListService({ data: JSON.stringify(reqData) })
        .then(result => {
          console.log("findOpenType---result");
          console.log(JSON.stringify(result));
          //
          if (result.success) {
            this.doorList = result.doorInfos;
            this.LLingID = result.lingLingId;
            this.findOpenType();
            this.$store.state.home.hasOpenDoor = true;
          }
        })
        .catch(error => {
          //
          this.phoneDoorDefaule = true; //default
          this.$toast("");
        });
    },
    /*  */
    findOpenType() {
      console.log("findOpenType");
      for (let i in this.doorList) {
        let openType = this.doorList[i].openType;
        //1357,
        if ((openType & 1) == 1) {
          console.log("");
          if (this.doorList[i].deviceMan == "") {
            this.isLLBluetooth = true;
          } else if (this.doorList[i].deviceMan == "") {
            this.isLeeLenBluetooth = true;
          }
        }

        //
        if ((2 & openType) == 2) {
          if (this.doorList[i].deviceMan == "") {
            console.log("");
            this.isLLQrcode = true;
          }
        }
        //openType=4
        if ((4 & openType) == 4) {
          console.log("");
          this.isRemote = true;
        }
      }

      if (this.LLingID != "" && this.LLingID != null) {
        this.getLinglingDoorInfo(this.LLingID);
      }
    },
    /*  */
    getLinglingDoorInfo() {
      let reqData = {
        AREA_CODE: this.$userInfo.AREA_CODE,
        BUILD_CODE: this.$userInfo.BUILD_CODE,
        UNIT_CODE: this.$userInfo.UNIT_NO,
        ROOM_CODE: this.$userInfo.ROOM_CODE
      };
      getLinglingDoorInfoService({ data: JSON.stringify(reqData) })
        .then(result => {
          //
          if (result.success) {
            this.linglingDoorInfos = result.doorInfos;
            var doorInfos = result.doorInfos;
            var sdkKeys = "";
            for (var i = 0; i < doorInfos.length; i++) {
              if (i == 0) {
                sdkKeys = sdkKeys + doorInfos[i].sdkKey;
              } else {
                sdkKeys = sdkKeys + "," + doorInfos[i].sdkKey;
              }
            }
            this.linglingKeys = sdkKeys;
          }
        })
        .catch(error => {
          //
          this.phoneDoorDefaule = true; //default
          this.$toast("");
        });
    },
    /*  */
    doorPress() {
      console.log("doorPress");
      (this.bindThemeL = this.$curTheme == 0 ? false : true),
        //
        (this.isOpening = true);
      //
      this.openStart = new Date();
      //
      this.btnTimer = setTimeout(() => {
        this.isOpening = false;
      }, 8000);
      //
      setTimeout(() => {
        //
        if (this.isLLQrcode) {
          this.showRemotePopup();
        } else {
          if (this.doorList.length > 1 && this.isRemote) this.showRemotePopup();
        }
      }, 2000);
      //
      if (this.doorList == null || this.doorList.length == 0) {
        this.$toast("!");
        this.closePopup();
        return;
      }
      //
      if (this.isLLBluetooth && this.isLeeLenBluetooth) {
        this.openLLBlueTooth();
      }
      //
      else if (this.isLLBluetooth || this.isLeeLenBluetooth) {
        if (this.isLLBluetooth) {
          this.openLLBlueTooth();
        }
        if (this.isLeeLenBluetooth) {
          this.openLeeLenBlueTooth();
        }
      } else {
        //
        this.isOnlyOneDoorRemote();
      }
    },
    /* ************************************************************************** */
    /*  */
    openLLBlueTooth() {
      var lingling = cordova.plugins.linglingPlugin;
      lingling.btOpenDoorMethod(
        this.linglingKeys,
        resultStatus => {
          console.log("======linglinglanyachenggong");
          this.closePopup();

          this.$toast({
            message: "",
            onClose: () => {
              //   
              for (var i = 0; i < this.linglingDoorInfos.length; i++) {
                console.log("for resultStatus=" + resultStatus);
                // 
                if (this.linglingDoorInfos[i].deviceCode == resultStatus) {
                  console.log(
                    " deviceCode=" + this.linglingDoorInfos[i].deviceCode
                  );
                  for (var j = 0; j < this.doorList.length; j++) {
                    console.log("for");
                    //id
                    if (
                      this.linglingDoorInfos[i].sdkKey ==
                      this.doorList[j].sdkKey
                    ) {
                      console.log(" sdkKey=" + this.doorList[j].sdkKey);
                      //  
                      this.addRecord(
                        this.doorList[j].deviceId,
                        this.doorList[j].deviceName,
                        1,
                        2,
                        0,
                        "",
                        1,
                        ""
                      );

                      this.addPoint();
                    }
                  }
                }
              }
            }
          });
        },
        message => {
          this.closePopup();
          console.log("" + message);
          // 
          this.addRecord("", "", 1, 2, 0, "", 0, message);
          if (this.isLeeLenBluetooth) {
            this.openLeeLenBlueTooth();
            return;
          }
          if (!this.isRemote && !this.isLLQrcode) {
            this.$toast("");
            return;
          }
          this.isOnlyOneDoorRemote();
        }
      );
    },
    /*  */
    openLeeLenBlueTooth() {
      var reqData = {
        neigh_no: this.$userInfo.AREA_NO,
        neigh_structure: this.$userInfo.ROOM_NO,
        ROOM_CODE: this.$userInfo.ROOM_CODE
      };
      getLeeLenDoorInfoService({ data: JSON.stringify(reqData) })
        .then(res => {
          if (res.success == "success") {
            var llData = res.door_entry;
            llData = JSON.stringify(llData);
            var obj = JSON.parse(llData);
            obj.isLLBluetooth = this.isLLBluetooth;
            if (/android/.test(navigator.userAgent.toLowerCase())) {
              this.leelenAndroid(obj);
            } else {
              this.leelenIos(obj);
            }
          }
        })
        .catch(err => {
          console.log(JSON.stringify(err));
          this.$toast("");
        });
    },
    leelenIos(obj) {
      /*  */
      window.broadcaster.fireNativeEvent(
        "openLeelenDoor",
        { openCode: obj },
        function() {
          console.log("");
        }
      );

      var leelenBlueFlag = 0;
      // ;
      window.broadcaster.addEventListener("LeeLenResultCode", data2 => {
        if (data2.code == "0" && leelenBlueFlag == 0) {
          this.$toast({
            message: "",
            onClose: () => {
              //  
              this.addRecord(
                data2.DEVICE_ID,
                data2.DEVICE_NAME,
                1,
                1,
                0,
                "",
                1,
                ""
              );

              this.addPoint();
            }
          });

          this.closePopup();
        } else if (leelenBlueFlag == 0) {
          console.log("");
          this.closePopup();
          //  
          var errMsg = this.getLeelenErrorMsg(data2.code);
          this.addRecord("", "", 1, 1, 0, "", 0, errMsg);

          if (!this.isRemote && !this.isLLQrcode) {
            this.$toast("");
            return;
          }
          this.isOnlyOneDoorRemote();
        }
        leelenBlueFlag = 1;
      });
    },
    leelenAndroid(obj) {
      var leelen = cordova.plugins.LeeLenPlugin;
      leelen.access(
        obj,
        resultStatus => {
          console.log("line 335!!!!!_+_+_+_+_ " + resultStatus);
          this.$toast({
            message: "",
            onClose: () => {
              this.addRecord(
                resultStatus.deviceId,
                resultStatus.deviceName,
                1,
                1,
                0,
                "",
                1,
                ""
              );

              this.addPoint();
            }
          });

          this.closePopup();
        },
        message => {
          console.log("===countFlag2=== " + message);
          //  
          var errMsg = this.getLeelenErrorMsg(message);
          this.addRecord("", "", 1, 1, 0, "", 0, errMsg);
          this.closePopup();
          if (!this.isRemote && !this.isLLQrcode) {
            this.$toast("");
            return;
          }
          this.isOnlyOneDoorRemote();
        }
      );
    },
    getLeelenErrorMsg(message) {
      var errMsg = "";
      switch (message) {
        case "10001":
          errMsg = "10001:";
          break;
        case "10002":
          errMsg = "10002:";
          break;
        case "10003":
          errMsg = "10003:";
          break;
        case "10004":
          errMsg = "10004:";
          break;
        case "10005":
          errMsg = "10005:";
          break;
        case "10006":
          errMsg = "10006:";
          break;
        case "10007":
          errMsg = "10007:";
          break;
      }
      return errMsg;
    },
    /* ************************************************************************** */
    showRemotePopup() {
      this.isPopShow = true;
      //
      this.$nextTick(() => {
        this.getQrCanvas();
      });

      this.popupTimer = setTimeout(() => {
        this.addPoint();
        this.closePopup();
      }, 15000);
    },
    getQrCanvas() {
      if (this.linglingKeys != "" && this.linglingKeys != null) {
        this.qrCanvas = document.getElementById("phoneDoorQrcode");
        console.log("canvas=" + this.qrCanvas);
        var lingling = cordova.plugins.linglingPlugin;
        lingling.getQrCode(
          this.LLingID,
          this.linglingKeys,
          resultStatus => {
            console.log("" + resultStatus);
            // valmsgcanvas
            console.log("sdkKeys=" + this.linglingKeys);
            QRCode.toCanvas(this.qrCanvas, resultStatus);
          },
          function(message) {
            console.log("");
          }
        );
      }
    },
    closePopup() {
      this.isOpening = false;
      this.isPopShow = false;
      clearTimeout(this.popupTimer);
      clearTimeout(this.btnTimer);
    },
    /*  */
    isOnlyOneDoorRemote() {
      if (this.doorList.length == 1 && this.isRemote)
        this.doorRemoteOpen(this.doorList[0]);
    },
    /*  */
    doorRemoteOpen(obj) {
      if (obj.deviceMan == "") {
        this.openLinglingRemoteDoor(obj);
      } else {
        this.openLeeLenRemoteDoor(obj);
      }
    },
    /*  */
    openLinglingRemoteDoor(door) {
      let reqData = {
        sdkKey: door.sdkKey.toString()
      };
      openLinglingRemoteDoorService({ data: JSON.stringify(reqData) })
        .then(result => {
          console.log(JSON.stringify(result));
          if (result.success) {
            //
            this.closePopup();
            this.$toast({
              message: "",
              onClose: () => {
                this.addRecord(
                  door.deviceId,
                  door.deviceName,
                  2,
                  2,
                  0,
                  "",
                  1,
                  ""
                );
                this.addPoint();
              }
            });
          } else {
            //
            this.closePopup();
            this.$toast({
              message: result.msg,
              onClose: () => {
                this.addRecord(
                  door.deviceId,
                  door.deviceName,
                  2,
                  2,
                  0,
                  "",
                  0,
                  result.msg
                );
              }
            });
          }
        })
        .catch(error => {
          this.$toast("");
        });
    },
    /*  */
    openLeeLenRemoteDoor(door) {
      let reqData = {
        neigh_no: door.neigh_no.toString(),
        neigh_structure: door.neigh_structure.toString(),
        device_no: door.deviceId.toString(),
        read_head_no: door.read_head_no.toString()
      };
      openLeeLenRemoteDoorService({ data: JSON.stringify(reqData) })
        .then(result => {
          if (result.success == "success") {
            //
            this.closePopup();
            console.log("");
            this.$toast({
              message: "",
              onClose: () => {
                console.log("toast close");
                this.addRecord(
                  door.deviceId,
                  door.deviceName,
                  2,
                  1,
                  0,
                  "",
                  1,
                  ""
                );

                this.addPoint();
              }
            });
          } else {
            //
            this.closePopup();
            this.$toast({
              message: "",
              onClose: () => {
                this.addRecord(
                  door.deviceId,
                  door.deviceName,
                  2,
                  1,
                  0,
                  "",
                  0,
                  result.msg
                );
              }
            });
          }
        })
        .catch(error => {
          this.$toast("");
        });
    },

    /* ************************************************************************** */
    /*  */
    addRecord(
      deviceId,
      deviceName,
      openType,
      deviceMan,
      doorType,
      duration,
      openSucceed,
      remarks
    ) {
      //
      var phoneModel = device.model;
      var phoneType = device.platform;

      //
      var openEnd = new Date();
      duration = openEnd.getTime() - this.openStart.getTime();
      let reqData = {
        areaCode: this.$userInfo.AREA_CODE, //
        areaName: this.$userInfo.AREA_NAME, //
        buildCode: this.$userInfo.BUILD_CODE, //
        buildName: this.$userInfo.BUILD_NAME, //
        unitNo: this.$userInfo.UNIT_NO, //
        roomCode: this.$userInfo.ROOM_CODE, //
        ownerCode: this.$userInfo.OWNER_CODE, //
        ownerName: this.$userInfo.OWNER_NAME, //
        ownerNickName: this.$userInfo.OWNER_NICK_NAME, //
        phone: this.$userInfo.PHONE, //
        phoneModel: phoneModel, // 
        phoneType: phoneType, // 
        deviceId: deviceId, // 
        deviceName: deviceName, // 
        openType: openType, // 1230
        deviceMan: deviceMan, // 0 1 2 3 4
        doorType: doorType, // 0 1
        duration: duration, //
        openSucceed: openSucceed, // 0 1
        remarks: remarks //
      };
      if (this.$userInfo.AREA_NO == "" || this.$userInfo.AREA_NO == undefined) {
        // AREA_NO
        reqData.DOOR_TYPE = "1";
      } else {
        reqData.DOOR_TYPE = "0"; //0,1;
      }
      reqData.DOOR_TYPE = "0";
      addRecordService(reqData)
        .then(result => {
          //
          if (result.success) {
            //
          }
        })
        .catch(error => {
          //
          this.phoneDoorDefaule = true; //default
        });
    },
    addPoint() {
      var reqData = {
        areaCode: this.$userInfo.AREA_CODE,
        behaviourNo: "1002",
        ownerCode: this.$userInfo.OWNER_CODE,
        appVersion: "3.1.0.0"
      };
      addPointService(reqData)
        .then(result => {
          //
          if (result.code == 200) {
            this.$toast("" + result.data + "");
          }
        })
        .catch(error => {
          //
          this.phoneDoorDefaule = true; //default
          this.$toast("");
        });
    }
    /* ************************************************************************** */
  }
};
</script>

<style scoped lang="scss">
