  .popcloseBtn {
    width: 28px;
    height: 28px;
  }
  .door-popup-body {
    .failText {
      font-size: 18px;
    }
    .phoneDoorQrcode {
      height: 190px;
      width: 190px;
    }
    .failMsg {
      font-size: 18px;
    }
    .share {
      padding: 0 4% 0 4%;
    }
    .share .leftImg .doorBtn {
      width: 125px;
      height: 40px;
      border-radius: 500px;
      font-size: 14px;
    }
    .share .leftImg .doorBtn:active {
      width: 125px;
