import Vue from 'vue'
import App from './App.vue'
import { router } from './router';
// import lib-flexible
import 'amfe-flexible'

import store from "./store/index";
import PluginAdapter from "./pluginManager/pluginAdapter";
import echarts from 'echarts'
// vant
import Vant from 'vant';
import 'vant/lib/index.css';
import { storage } from './utils/storage'
import { Lazyload } from 'vant'
//RSA
import JSEncrypt from 'jsencrypt';
Vue.prototype.$jsencrypt = JSEncrypt;

import localImg from './utils/localImg'; //

// options 
Vue.use(Lazyload, {
  error: require('@/assets/images/default/default_picture.png'), // 
  loading: require('@/assets/images/default/default_picture.png') // 
});
Vue.use(Vant);
Vue.use(VueHtml5Plus);
Vue.config.productionTip = false;
Vue.config.bus = new Vue();
new PluginAdapter(function (pluginMap) {
  Vue.prototype.$plugin = pluginMap
  // echarts
  Vue.prototype.$echarts = echarts
  //
  Vue.prototype.$isLogin = false;
  //
  Vue.prototype.$refreshHomePage = false;
  //
  Vue.prototype.$userInfo = null;
  // 
  Vue.prototype.$fromPage = '';
  //
  Vue.prototype.$isShowKeyboard = false;

  //  userInfo setStoragestorage
  Vue.prototype.$setUserInfo = function (userInfo, updateStorage = true) {
    if (userInfo == undefined
      || userInfo == null) {
      Vue.prototype.$isLogin = false;
      Vue.prototype.$userInfo = null;
      if (updateStorage) {
        //
        storage.deleteStorage('userInfo');
      }
    }
    else {
      Vue.prototype.$isLogin = true;
      Vue.prototype.$userInfo = userInfo;
      if (updateStorage) {
        //localStorage
        storage.setStorage('userInfo', userInfo, 60);
      }
    }
  };

  //0
  Vue.prototype.$stbHeight = 0;
  //, default:light:
  Vue.prototype.$curStbStyle = "default";

  //0
  Vue.prototype.$footBarHeight = 0;

  //
  Vue.prototype.$setStbLightStyle = function () {
    Vue.prototype.$curStbStyle = "light";
    //console.log("setStbLightStyle: stbHeight = " + this.$stbHeight + ", platform = " + this.$devicePlatform);
    if (this.$stbHeight > 0 && this.$devicePlatform != 'pc') {
      StatusBar.styleLightContent();
    }
  };

  //
  Vue.prototype.$setStbDefaultStyle = function () {
    Vue.prototype.$curStbStyle = "default";
    //console.log("setStbDefaultStyle: stbHeight = " + this.$stbHeight + ", platform = " + this.$devicePlatform);
    if (this.$stbHeight > 0 && this.$devicePlatform != 'pc') {
      StatusBar.styleDefault();
    }
  };

  //
  Vue.prototype.$updateStb = function (to) {
    console.log("updateStb: stbHeight = " + this.$stbHeight + ", platform = " + this.$devicePlatform);
    if (this.$stbHeight > 0 && this.$devicePlatform != 'pc') {
      /*if (cordova.platformId == 'android') { //  cordova 
        console.log("updateStb: ");
        StatusBar.overlaysWebView(true);
      }*/
      if (to.meta.statusBgc) {
        if (Vue.prototype.$curStbStyle != "light") {
          console.log("updateStb: ");
          StatusBar.styleLightContent();
          Vue.prototype.$curStbStyle = "light";
        } else {
          console.log("updateStb: ");
        }
      } else {
        if (Vue.prototype.$curStbStyle != "default") {
          console.log("updateStb: ");
          StatusBar.styleDefault();
          Vue.prototype.$curStbStyle = "default";
        } else {
          console.log("updateStb: ");
        }
      }
      //to.meta.statusBgc ? StatusBar.styleLightContent() : StatusBar.styleDefault()

    }
  };

  //0: 1
  Vue.prototype.$curTheme = "0";

  //
  Vue.prototype.$openVirArea = true;

  //
  Vue.prototype.$needUpOwner = false;

  Vue.filter('keepTwoNum', function (value) {
    value = Number(value);
    return value.toFixed(2);
  });

  new Vue({
    router,
    store,
    render: h => h(App),
  }).$mount('#app')
});

// //jsrsasign
Vue.prototype.$getRSASign = function (password, key) {
  let sign = new JSEncrypt({
    default_key_size: 1024
  });
  key = key.replace("/r", "");
  console.log(key);
  key = "-----BEGIN PRIVATE KEY-----" + key + "-----END PRIVATE KEY-----"
  sign.setPublicKey(key);
  let data = sign.encrypt(password.toString());
  return data;
};



