const state = {
    count: 5,
    // tab
    activeTab: 0,
}

//  getters
var getters = {
    cbd(state) {
        return state.count +'sdsd'
    }
}

const actions = {
    //tab
    changeActiveTabFun(context, index) {
        context.commit('changeActiveTab', index);
    },
    increment({ commit, state }) {
        //  mutations  types.INCREMENT 
        // commit('increment') 
        //
        setTimeout(()=>{
            commit('add');
        },2000)
    },
    decrement({ commit, state }) {
        if (state.count > 10) {
            commit('minus')
        }
    }
}

const mutations = {
    // tab
    changeActiveTab(state, index) {
        state.activeTab = index;
    },
    //  actions  commit(types.INCREMENT)
    add(state) {

        state.count++
    },
    //  actions  commit(types.DECREMENT)
    minus(state) {
        state.count--
    }
}
// 
export default {
    state,
    getters,
    actions,
    mutations
}
