const actions = {
    increment({ commit, state }) {
        //  mutations  types.INCREMENT 
        // commit('increment') 
        //
        setTimeout(() => {
            commit('add');
        }, 2000)
    },
    decrement({ commit, state }) {
        if (state.count > 10) {
            commit('minus')
        }
    }
}

const mutations = {
    // userInfo
    setUserInfo(state, userInfo) {
        state.userInfo = userInfo;
    },
    //  actions  commit(types.DECREMENT)
    minus(state) {
        state.count--
    }
}
// 
export default {
    state,
    actions,
    mutations
}
